body {
  background-color: #141414;
  margin: 0;
  font-family: Montserrat, sans-serif;
}

@font-face {
  font-family: Ninja Naruto;
  src: url("Ninja Naruto.44633dd6.ttf");
}

.header {
  justify-content: space-between;
  padding: 20px 85px;
  display: flex;
}

.header__title {
  color: #f97905;
  font-family: Ninja Naruto;
  font-size: 30px;
  text-decoration: none;
}

.header__buttons-box {
  gap: 130px;
  display: flex;
}

.header__button {
  color: #f97905;
  background-color: inherit;
  cursor: pointer;
  border: 0;
  padding: 0;
  font-family: Ninja Naruto;
  font-size: 30px;
}

.hero {
  padding: 20px 85px;
}

.hero__title {
  color: #f97905;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  text-align: center;
  font-size: 30px;
  font-weight: 900;
}

.hero__text {
  color: #f97905;
  letter-spacing: 1.2px;
  text-indent: 20px;
  font-size: 20px;
  line-height: 25px;
}

.section__form {
  padding: 20px 85px;
}

.form {
  width: 1000px;
  border: 1px solid #f97905;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  margin: auto;
  padding: 15px;
  display: flex;
}

.form__label {
  color: #f97905;
  letter-spacing: 1.2px;
  text-align: center;
  font-size: 20px;
  font-weight: 900;
}

.form__input {
  width: 500px;
  height: 36px;
  color: #fff;
  background-color: #ad5400;
  border: none;
  border-radius: 18px;
  padding: 5px 10px;
  font-size: 20px;
}

.form__input::-ms-input-placeholder {
  color: #141414;
}

.form__input::placeholder {
  color: #141414;
}

.form__input:not(:placeholder-shown) {
  background-color: #f97905;
}

.form__input:focus {
  background-color: #f97905;
  outline: 2px solid #fff;
}

.form__button {
  width: 120px;
  height: 30px;
  color: #141414;
  cursor: pointer;
  background-color: #f97905;
  border: none;
  border-radius: 4px;
  font-size: 20px;
}

.form__button:focus, .form__button:hover {
  color: #fff;
  outline: 2px solid #fff;
}

